import style from './styles.module.scss'
import logo from '../../assets/logo.svg'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons'
import { useState } from 'react'

export function Header() {
  const [isMenuVisible, setMenuVisible] = useState(false)

  function toggleMenuVisibility(event) {
    event.preventDefault()
    setMenuVisible(!isMenuVisible)
  }

  return (
    <div className={style['custom-navbar']}>
      <nav>
        <a href="/">
          <img src={logo} alt="logo" />
        </a>

        <ul>
          <span className={isMenuVisible ? style.visible : 'hidden'}>
            <a href="/passive_web">Home</a>
            <a href="#cardsSection">Features</a>
            <a href="#roadmapSection">Road Map</a>
            <a href="#howToBuySection">How to Buy</a>
            <a href="/passive_web/whitePaper">White Paper</a>
            <a href="/passive_web/vesting">Vesting</a>

            {isMenuVisible && (
              <FontAwesomeIcon
                icon={faTimes}
                className={style.close}
                onClick={toggleMenuVisibility}
              />
            )}
          </span>

          <a href="/" onClick={toggleMenuVisibility}>
            <FontAwesomeIcon icon={faBars} className={style.hamburguer} />
          </a>
        </ul>
      </nav>
    </div>
  )
}
