import './style.scss'
import { useClaimRewards } from '../../../../hooks/useClaim'

interface ButtonProps {
  text: string
  buttonStyle: 'card-button' | 'full-width'
}

export function Button({ text, buttonStyle }: ButtonProps) {
  const { claim } = useClaimRewards()

  function handleOnClick() {
    claim()
  }

  return (
    <div id="claim-button">
      <button className={buttonStyle} onClick={() => handleOnClick()}>
        {text}
      </button>
    </div>
  )
}
