import { Header } from './components/Header'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { Home } from './pages/Home'
import { WhitePaper } from './components/WhitePaper'
import { Footer } from './components/Footer'
import './styles/GlobalStyle.scss'
import { DashboardClaim } from './pages/DashboardClaim'
import { Swap } from './pages/Swap'
import { DashboardVesting } from './pages/DashboardVest'

export function App() {
  return (
    <>
      <div className="App">
        <Router>
          <Header />
          <Switch>
            <Route path="/passive_web" exact component={Home} />
            <Route
              path="/passive_web/whitePaper"
              exact
              component={WhitePaper}
            />
            <Route path="/passive_web/claim" exact component={DashboardClaim} />
            <Route path="/passive_web/swap" exact component={Swap} />
            <Route
              path="/passive_web/vesting"
              exact
              component={DashboardVesting}
            />
          </Switch>
          <Footer />
        </Router>
      </div>
    </>
  )
}
